.BigMenu {
    display: flex;
    justify-content: space-around;
    align-items: center;         
    flex-direction: row;
    height:750px;
    padding-top: 250px;  
    padding-left: 150px;    
    padding-right: 150px; 
}
.BM_item {
   margin-top: -500px;
   width: 170px;
   height: 600px;
   background-repeat: no-repeat;
   background-size: cover;
   border-radius: 10px;
   
   box-shadow: 12px 29px 81px 0px rgba(140,140,140,0.75);
}
.BM_slider__btn__left, .BM_slider__btn__right {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 50%;
    margin-top: -30px;   
    left: 5px; 
}
.BM_slider__btn__right {
    left: 1010px;
}
.BM_item:hover {
    box-shadow: 12px 29px 81px 0px rgba(0,0,0,0.75);
}
#BM_item0 { background-image: url("./menuB00.png")}
#BM_item1 { background-image: url("./menuB10.png")}
