@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
.LoginPage {
    position: absolute;
    width: 300px;
    height: 300px;
    top: 50px;
    left: 65%;
    display: flex;
    align-content: center;
    justify-content: space-around;
    flex-wrap: wrap;   
}

.LoginPage_Login, .LoginPage_Pass, .LoginPage_But {
    width: 60%;
    margin-top: 15px;
    margin-bottom: 15px;   
    border-radius: 5px; 
    font: 20px "RobotoRegular";
    text-align: center;
    outline: none;
}